* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
}

:root {
    --bgcolor: #f1f5fd;
    --primary-color: #422774;
    --text-color: #333333;
    --main-text-color: #e5322d;
}

html {
    font-size: 62.5%;
}

a {
    text-decoration: none;
    color: #fff;
}

h1, h2, h3, h4, h5, h6 {
    color: #333333;
}

/*--navbar css ---*/

.navbar-bg {
    background-color: var(--bgcolor);
    padding: 2rem 0;
    z-index: 99;
    position: relative;
} 

.navbar-brand {
    font-size: 2.5rem;
}

.navbar li {
    padding: 0 1.5rem;
}

.navbar li a {
    font-size: 1.2rem;
    position: relative;
}

.navbar li a:hover {
    color: var(--primary-color) !important;
}

