/*--hero section ---*/

header {
    background-color: var(--bgcolor);
    min-height: 100vh;
    width: 100%;
}

.main-hero-container {
    height: 80vh;
    display: flex;
}

.main-hero-container h1 {
    font-weight: bolder;
    color: var(--primary-color);
}

.main-hero-para {
    font-size: 1.5rem;
    color: var(--text-color);
    margin: 3rem 0 5rem 0;
    
    width: 75%;
}

.main-herosection-images {
    position: relative;
}

.main-herosection-images img {
    width: 60%;
    height: 75%;
    margin-top: 10rem;
    border-radius: 1.5rem;
    position: absolute;
}

.main-herosection-images .main-hero-img2 {
    position: absolute;
    width: 50%;
    height: 30%;
    left: 55%;
    top: -2%;
}

.input-group-button {
    
    color: #fff;
    background-color: var(--primary-color);
    height: 4rem;
    width: 50%;
    border-radius: 2rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

/*--How does it work ---*/

.work-container {
    width: 80vw;
    padding: 5rem;
    box-shadow: 0 0.4rem 1.2rem rgba(0, 0, 0, 0.1);
    margin-top: -10rem;
    background-color: #fff;
    border-radius: 0.5rem;
}

.work-container .main-heading {
    font-size: 3rem;
    font-weight: bolder;
    margin-bottom: 4rem;
    word-spacing: 1rem;
}

.work-container .sub-heading {
    font-size: 2rem;
    font-weight: normal;
}

.fontawesome-style {
    font-size: 2.3rem;
    color: var(--primary-color);
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    background-color: var(--main-text-color);
    display: grid;
    place-items: center;
    margin: 2rem auto;
}

/*--About Us ---*/

.common-section {
    margin: 5rem;
}

.our-services img {
    width: 40%;
    height: 75%;
    margin-top: 10rem;
    border-radius: 1.5rem;
}

.our-services .mini-title {
    text-transform: uppercase;
    font-weight: normal;
    margin: 3rem 0;
    text-align: left;
    font-size: 1.5rem;
}

.our-services .main-heading {
    font-weight: bolder;
}

.our-services .our-services-info {
    margin-top: 4rem;
}

.our-services .our-services-number {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background-color: transparent;
    border: 0.1rem solid var(--primary-color);
    display: grid;
    place-items: center;
}

.our-services-rightside-content {
    padding-left: 8rem;
}

/*--Services ---*/

.service-main-container {
    margin-top: 5rem;
    padding: 5rem 0;
}

.service-main-container .main-heading {
    margin-inline-end: 3rem 0;
}

.work-container-subdiv {
    box-shadow: 0 0.1rem 0.4rem rgba(0, 0, 0, 0.16);
}

.work-container-subdiv:hover {
    background-color: #6a86b6;
    cursor: pointer;
}

.work-container-subdiv:hover> .fontawesome-style {
    background-color: #fff;
}

.work-container-subdiv .main-hero-para {
    width: 100%;
}

.service-main-container .fontawesome-style {
    text-align: left;
    margin: 2rem 0;
}

@media (min-width: 1400px) {
    .service-container .col-xxl-4 {
        flex: 0 0 auto;
        width: 30% !important;
        margin: auto;
    }
}

@media (min-width: 992px) {
    .service-container .col-lg-4 {
        flex: 0 0 auto;
        width: 30% !important;
        margin: 2.5rem auto;
    }
}

/*--responsive media queries ---*/

@media (min-width: 1500px) {
    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container-xl,
    .container-xxl {
        max-width: 1500px !important;
    }
}

@media (min-width: 1400px) {
    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container-xl,
    .container-xxl {
        max-width: 1400px;
    }
}






























